import React from "react";
import { updateLoginStatus } from "../../redux/actions/loginStatusActions";
import GoogleLogin from "react-google-login";
// import userProfile from "../common/userProfile";
import PropTypes from "prop-types";
import { connect } from "react-redux";

const HomePage = ({ history, updateLoginStatus }) => {
  const responseGoogleAdmin = (response) => {
    console.log(response);
    localStorage.setItem("auth", "admin");
    updateLoginStatus("admin");
    history.push("/admin/dashboard");
  };

  const responseGoogleStaff = (response) => {
    console.log(response);
    localStorage.setItem("auth", "staff");
    updateLoginStatus("staff");
    history.push("/staff/dashboard");
  };
  const responseGoogleError = (response) => {
    console.log(response);
    alert("Login failed: " + response);
  };
  return (
    <div className="jumbotron">
      <div className="row">
        <div className="col-6">
          <div>
            <button
              onClick={() => {
                localStorage.setItem("auth", "admin");
                updateLoginStatus("admin");
                history.push("/admin/dashboard");
              }}
              className="btn btn-primary btn-lg"
            >
              Login as Admin
            </button>
          </div>
          <div style={{ paddingTop: 10 }}>
            <GoogleLogin
              clientId="1043716321761-s72j1m8lss8gubgrn8032qebgbsdabme.apps.googleusercontent.com"
              buttonText="LOGIN WITH GOOGLE"
              onSuccess={responseGoogleAdmin}
              onFailure={responseGoogleError}
            />
          </div>
        </div>
        <div className="col-6">
          <div>
            <button
              onClick={() => {
                localStorage.setItem("auth", "staff");
                updateLoginStatus("staff");
                history.push("/staff/dashboard");
              }}
              className="btn btn-primary btn-lg"
            >
              Login as Staff
            </button>
          </div>
          <div style={{ paddingTop: 10 }}>
            <GoogleLogin
              clientId="1043716321761-s72j1m8lss8gubgrn8032qebgbsdabme.apps.googleusercontent.com"
              buttonText="LOGIN WITH GOOGLE"
              onSuccess={responseGoogleStaff}
              onFailure={responseGoogleError}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

HomePage.propTypes = {
  history: PropTypes.object.isRequired,
  loginStatus: PropTypes.string,
  updateLoginStatus: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
  return { loginStatus: state.loginStatus };
}

const mapDispatchToProps = {
  updateLoginStatus,
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
